<template>
    <el-aside class="aside" width="260px">
        <el-row class="tac" style="margin-bottom:0px">
            <el-col :span="24">
                <el-menu :default-active="defaultActive" class="el-menu-vertical-demo" @select="asideSelect">
                    <template>
                        <el-menu-item v-if="fedrole=='平台管理员'" v-for="item in asideList100" :key="item.index" :index="item.index" :class="defaultActive==item.index?'active':''">
                            <img :src="`static/images/icon`+item.index+`_a.png`" v-if="defaultActive==item.index" :style="item.index==6?'height:27px;margin:26.5px 20px 26.5px 25px;':'height:24px;margin:28px 20px 28px 25px;'" alt="">
                            <img :src="`static/images/icon`+item.index+`_b.png`" v-if="defaultActive!=item.index" :style="item.index==6?'height:27px;margin:26.5px 20px 26.5px 25px;':'height:24px;margin:28px 20px 28px 25px;'" alt="">
                            <span slot="title">{{item.title}}</span>
                        </el-menu-item>
                        <el-menu-item v-if="fedrole=='节点管理员'" v-for="item in asideList200" :key="item.index" :index="item.index" :class="defaultActive==item.index?'active':''">
                            <img :src="`static/images/icon`+item.index+`_a.png`" v-if="defaultActive==item.index" :style="item.index==6?'height:27px;margin:26.5px 20px 26.5px 25px;':'height:24px;margin:28px 20px 28px 25px;'" alt="">
                            <img :src="`static/images/icon`+item.index+`_b.png`" v-if="defaultActive!=item.index" :style="item.index==6?'height:27px;margin:26.5px 20px 26.5px 25px;':'height:24px;margin:28px 20px 28px 25px;'" alt="">
                            <span slot="title">{{item.title}}</span>
                        </el-menu-item>
                        <el-menu-item v-if="fedrole=='节点普通用户'" v-for="item in asideList300" :key="item.index" :index="item.index" :class="defaultActive==item.index?'active':''">
                            <img :src="`static/images/icon`+item.index+`_a.png`" v-if="defaultActive==item.index" :style="item.index==6?'height:27px;margin:26.5px 20px 26.5px 25px;':'height:24px;margin:28px 20px 28px 25px;'" alt="">
                            <img :src="`static/images/icon`+item.index+`_b.png`" v-if="defaultActive!=item.index" :style="item.index==6?'height:27px;margin:26.5px 20px 26.5px 25px;':'height:24px;margin:28px 20px 28px 25px;'" alt="">
                            <span slot="title">{{item.title}}</span>
                        </el-menu-item>
                    </template>
                </el-menu>
            </el-col>
        </el-row>
    </el-aside>
</template>
<script>
import $ from "jquery";

export default {
    name: "asides",
    mounted() {},
    data: function() {
        return {
            defaultActive:'',
            asideList100:[
                {"index":1,"title":"节点管理"},
                {"index":2,"title":"账户管理"},
                {"index":3,"title":"资源监控"},
            ],
            asideList200:[
                {"index":4,"title":"节点设置"},
                {"index":5,"title":"场景设置"},
                {"index":6,"title":"数据字典"},
            ],
            asideList300:[
                {"index":7,"title":"数据管理"},
                {"index":8,"title":"模型训练"},
            ],
            fedname:Cookies.set("fedrolename")||"",
            fedrole:Cookies.set("fedrole")||"",
        }
    },
    mounted() {
        var url=location.href.split("#/")[1].split("&")[0];
        /* 对应选中栏 */
        if(url=='nodeManage'){
            this.defaultActive=1
        }
        else if(url=='account'){
            this.defaultActive=2
        }
        else if(url=='resMonitor'){
            this.defaultActive=3
        }
        else if(url=='nodeSet'){
            this.defaultActive=4
        }
        else if(url=='senceSet'){
            this.defaultActive=5
        }
        else if(url=='dataDic'){
            this.defaultActive=6
        }
        else if(url=='dataManage'||url=='dataWork'){
            this.defaultActive=7
        }
        else if(url=='modelTrain'){
            this.defaultActive=8
        }
        else if(url=='modelUse'){
            this.defaultActive=9
        }
    },
    methods: {
        asideSelect:function(key, keyPath) {
            Cookies.set("isReload2",false)
            if(key==1){
                this.$router.push('/nodeManage')
            }
            else if(key==2){
                this.$router.push('/account')
            }
            else if(key==3){
                this.$router.push('/resMonitor')
            }
            else if(key==4){
                this.$router.push('/nodeSet')
            }
            else if(key==5){
                this.$router.push('/senceSet')
            }
            else if(key==6){
                this.$router.push('/dataDic')
            }
            else if(key==7){
                this.$router.push('/dataManage')
            }
            else if(key==8){
                this.$router.push('/modelTrain')
            }
            else if(key==9){
                this.$router.push('/modelUse')
            }
        },
    },
};
</script>
<style>
    .el-submenu__title {
        padding-left: 0px;
        width: 100%;
        display: flex;
        height: 50px;
        margin-bottom: 25px;
        background: transparent;
        border-bottom-left-radius: 30px;
        border-top-left-radius: 30px;
        padding: 0px !important;
    }
    .aside {
        width: 260px;
        height: 100vh;
        min-width: 260px;
        max-width: 260px;
        position: fixed;
        left: 0;
        top: 80px;
        z-index: 999;
        background: #011e3c !important;
    }
    .el-menu-vertical-demo {
        height: 100vh;
        border: 0px;
        overflow-x: hidden;
        background-color: #011e3c !important;
    }
    .el-menu-vertical-demo li {
        display: flex;
        height: 80px;
        margin: 0px;
        background: transparent;
        padding: 0px 0px 0px 30px !important;
        border-left: 4px solid transparent;
    }
    .el-menu-vertical-demo li.active {
        display: flex;
        height: 80px;
        margin: 0px;
        background: #eef4fd;
        padding: 0px 0px 0px 30px !important;
        border-left: 4px solid #007dff;
    }
    .el-menu-vertical-demo li img{
        width: 24px;
        display: block;
        margin: 23px 20px 23px 25px;
        min-width: 24px;
        max-width: 24px;
    }
    .el-menu-vertical-demo li span{
        font-family: SourceHanSansCN-Regular;
        font-size: 16px;
        color: #697da7;
        letter-spacing: 0.67px;
        line-height: 80px;
    }
    .el-menu-vertical-demo li.active span{
        font-family: SourceHanSansCN-Regular;
        font-size: 16px;
        color: #007dff;
        letter-spacing: 0.67px;
        line-height: 80px;
    }
    .el-menu-item:focus, .el-menu-item:hover {
        outline:0;
        border-left: 4px solid #007dff;
        background: #eef4fd;
    }
</style>



